// src/TeamMember.js
import React from "react";

const TeamMember = ({ member }) => {
  return (
    <div className="team-member">
      <img src={member.photoUrl} alt={member.name} />
      <h3>{member.name}</h3>
      <p>{member.role}</p>
      <p>{member.bio}</p>
    </div>
  );
};

export default TeamMember;
