// src/TeamMembers.js
import React from "react";
import TeamMember from "./TeamMember";

const TeamMembers = ({ members }) => {
  return (
    <div className="team-members">
      {members.map((member, index) => (
        <TeamMember key={index} member={member} />
      ))}
    </div>
  );
};

export default TeamMembers;
